.ControlPoint {
	width: 2em;
	height: 2em;
	border-radius: 50%;
	border: 2px solid hsl(200, 100%, 40%);
	background-color: hsl(200, 100%, 90%);
	cursor: -webkit-grab;
	cursor: grab;
	-webkit-transform: translate(-1em, -1em);
	    -ms-transform: translate(-1em, -1em);
	        transform: translate(-1em, -1em);
}



